import React, { useState, useEffect } from "react";
import qs from "query-string";
import * as Yup from "yup";
import { useHistory } from "react-router-dom";
import { Link as RouterLink } from "react-router-dom";
import { toast } from "react-toastify";
import { Formik, Form, Field } from "formik";
import usePlans from "../../hooks/usePlans";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import { FormControl, InputLabel, MenuItem, Select } from "@material-ui/core";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import logo from "../../assets/logo2.png";
import backgroundStep from "../../assets/backgroundStep.png";
import { i18n } from "../../translate/i18n";
import "./style.css";
import { openApi } from "../../services/api";
import toastError from "../../errors/toastError";
import moment from "moment";

const useStyles = makeStyles((theme) => ({
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%",
    marginTop: theme.spacing(3),
  },
  submit: {
    "&.MuiButton-root": {
      margin: "20px 0px 16px",
      backgroundColor: "#A200FF",
      borderRadius: " 30px",
    },

    "&:hover": {
      backgroundColor: "#A200FF",
      boxShadow: "none",
    },

    backgroundColor: "#A200FF",
    margin: theme.spacing(3, 0, 2),
    WebkitTextFillColor: "#FFF",
  },
  whatsappButton: {
    margin: theme.spacing(3, 0, 2),
  },
}));

const handleNewUserMessage = (newMessage) => {
  window.open(
    `https://api.whatsapp.com/send?phone=5584998400790&text=${encodeURIComponent(
      newMessage
    )}`,
    "_blank"
  );
};

const validCodes = ["PROMOWN", "OTHER_CODE", "ANOTHER_CODE", "YET_ANOTHER_CODE"];

const UserSchema = Yup.object().shape({
  name: Yup.string()
    .min(2, "Too Short!")
    .max(50, "Too Long!")
    .required("Required"),
  password: Yup.string().min(5, "Too Short!").max(50, "Too Long!"),
  email: Yup.string().email("Invalid email").required("Required"),
});

const SignUp = () => {
  const classes = useStyles();
  const history = useHistory();
  let companyId = null;

  const params = qs.parse(window.location.search);
  if (params.companyId !== undefined) {
    companyId = params.companyId;
  }

  const initialState = {
    name: "",
    email: "",
    phone: "",
    password: "",
    planId: "",
  };

  const [user, setUser] = useState(initialState);
  const [isCodeEnabled, setIsCodeEnabled] = useState(false);
  const [validationCode, setValidationCode] = useState("");
  const [showPassword, setShowPassword] = useState(false);

  const dueDate = moment().add(7, "day").format();

  const handleSignUp = async (values) => {
    if (!validCodes.includes(validationCode)) {
      toast.error("Cdigo de validao invlido. No foi possvel registrar.");
      return;
    }

    Object.assign(values, { recurrence: "MENSAL" });
    Object.assign(values, { dueDate: dueDate });
    Object.assign(values, { status: "t" });
    Object.assign(values, { campaignsEnabled: true });

    try {
      await openApi.post("/companies/cadastro", values);
      toast.success(i18n.t("signup.toasts.success"));
      history.push("/login");
    } catch (err) {
      console.log(err);
      toastError(err);
    }
  };

  const [plans, setPlans] = useState([]);
  const { list: listPlans } = usePlans();

  useEffect(() => {
    async function fetchData() {
      const list = await listPlans();
      setPlans(list);
    }
    fetchData();
  }, []);

  const handleWhatsAppClick = () => {
    const message = `*Nome:* ${user.name}\n*E-mail:* ${user.email}\n*WhatsApp:* ${user.phone}\n*Plano:* ${user.planId}`;
    handleNewUserMessage(message);
    setIsCodeEnabled(true);
  };

  const togglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setUser((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  return (
    <div className="geral-signup">
      <div className={"container-signup"}>
        <div className={"paper"}>
          <img src={logo} alt="Whats" className="img-logo-signup" />

          <h4 className="h4" style={{ fontSize: "30px" }}>
            Cadastre-se
          </h4>
          <div>
            <span className="span" style={{ fontSize: "17px" }}>
              Comece seu <b>teste GRATUITO</b> de 7 dias do CRM da WhatsNeed!{" "}
            </span>
          </div>

          <Formik
            initialValues={user}
            enableReinitialize={true}
            validationSchema={UserSchema}
            onSubmit={(values, actions) => {
              setTimeout(() => {
                handleSignUp(values);
                actions.setSubmitting(false);
              }, 400);
            }}
          >
            {({ touched, errors, isSubmitting }) => (
              <Form className={classes.form}>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <Field
                      as={TextField}
                      autoComplete="name"
                      name="name"
                      error={touched.name && Boolean(errors.name)}
                      helperText={touched.name && errors.name}
                      variant="outlined"
                      fullWidth
                      id="name"
                      label="Seu Nome"
                      onChange={handleInputChange}
                    />
                  </Grid>
                  <Grid item xs={12}>
  <Field
    as={TextField}
    variant="outlined"
    fullWidth
    id="email"
    label="Seu Email"
    name="email"
    error={touched.email && Boolean(errors.email)}
    helperText={touched.email && errors.email}
    autoComplete="email"
    required
    onChange={handleInputChange}
  />
  <Typography variant="body2" color="textSecondary">
    Um e-mail vlido  necessrio para recuperar a senha.
  </Typography>
</Grid>
                  <Grid item xs={12}>
                    <Field
                      as={TextField}
                      variant="outlined"
                                            fullWidth
                      id="phone"
                      label="Seu Nmero de Telefone"
                      name="phone"
                      error={touched.phone && Boolean(errors.phone)}
                      helperText={touched.phone && errors.phone}
                      autoComplete="phone"
                      required
                      onChange={handleInputChange}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Field
                      as={TextField}
                      variant="outlined"
                      fullWidth
                      name="password"
                      error={touched.password && Boolean(errors.password)}
                      helperText={touched.password && errors.password}
                      label="Sua Senha"
                      type={showPassword ? "text" : "password"}
                      id="password"
                      autoComplete="current-password"
                      required
                      onChange={handleInputChange}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <InputLabel htmlFor="plan-selection">Plano</InputLabel>
                    <Field
                      as={Select}
                      variant="outlined"
                      fullWidth
                      id="plan-selection"
                      label="Plano"
                      name="planId"
                      required
                      onChange={handleInputChange}
                    >
                      {plans.map((plan, key) => (
                        <MenuItem key={key} value={plan.id}>
                          {plan.name} - Atendentes: {plan.users} - WhatsApp:{" "}
                          {plan.connections} - Filas: {plan.queues} - R${" "}
                          {plan.value}
                        </MenuItem>
                      ))}
                    </Field>
                  </Grid>
                  {isCodeEnabled && (
                    <Grid item xs={12}>
                      <Field
                        as={TextField}
                        variant="outlined"
                        fullWidth
                        name="validationCode"
                        label="Cdigo de Validao"
                        id="validationCode"
                        autoComplete="validation-code"
                        required
                        value={validationCode}
                        onChange={(e) => setValidationCode(e.target.value)}
                      />
                      {validCodes.includes(validationCode) ? (
                        <Typography variant="body1">Cdigo vlido</Typography>
                      ) : (
                        <Typography variant="body1">Cdigo invlido</Typography>
                      )}
                    </Grid>
                  )}
                </Grid>
                <Button
                  type="button"
                  fullWidth
                  variant="contained"
                  color="primary"
                  className={classes.submit}
                  onClick={handleWhatsAppClick}
                >
                  Solicitar Cdigo
                </Button>
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  color="primary"
                  className={classes.submit}
                >
                  {i18n.t("signup.buttons.submit")}
                </Button>
                <Grid container justify="flex-end">
                  <Grid item>
                    <Link
                      href="#"
                      variant="body2"
                      component={RouterLink}
                      to="/login"
                    >
                      {i18n.t("signup.buttons.login")}
                    </Link>
                  </Grid>
                </Grid>
              </Form>
            )}
          </Formik>
        </div>
        <div className="footer">
          <p className="p">
            Copyright {" "}
            <a href={"#"} target={"_blank"}>
              WhatsNeed
            </a>{" "}
            2024{" "}
          </p>
        </div>
        <Box mt={5}></Box>
      </div>
      <div className={"container-img-signup"}>
        <div className="img-signup"></div>
      </div>
    </div>
  );
};

export default SignUp;